import { AsyncPipe, CommonModule } from '@angular/common';
import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Observable } from 'rxjs';
import { User, UserService } from '../../../services/user.service';
import { FormBuilder, FormGroup, ReactiveFormsModule, Validators, AbstractControl, ValidationErrors, ValidatorFn } from '@angular/forms';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../../environments/environment';

declare var bootstrap: any;

@Component({
  selector: 'app-profile-info',
  standalone: true,
  imports: [CommonModule, ReactiveFormsModule],
  templateUrl: './profile-info.component.html',
  styleUrls: ['./profile-info.component.scss']
})
export class ProfileInfoComponent implements OnInit {
  user: User | null = null;
  changepassword!: FormGroup;
  showNewPassword: boolean = false;
  showConfirmPassword: boolean = false;
  @ViewChild('successToast', { static: true }) successToast!: ElementRef;
  @ViewChild('errorToast', { static: true }) errorToast!: ElementRef;

  constructor(
    private fb: FormBuilder,
    private userService: UserService,
    private http: HttpClient
  ) { }

  ngOnInit(): void {
    this.userService.getUser().subscribe(user => {
      this.user = user;
    });

    this.changepassword = this.fb.group(
      {
        newPassword: [
          '',
          [
            Validators.required,
            Validators.minLength(8),
            Validators.pattern(/^(?=.*[A-Z])(?=.*[a-z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]+$/)
          ]
        ],
        confirmPassword: ['', Validators.required]
      },
      { validators: this.passwordsMatchValidator }
    );
  }

  showSuccessToast() {
    const toast = new bootstrap.Toast(this.successToast.nativeElement);
    toast.show();
  }

  showErrorToast() {
    const toast = new bootstrap.Toast(this.errorToast.nativeElement);
    toast.show();
  }

  onSubmit(): void {
    if (this.changepassword.invalid) {
      this.showErrorToast();
      return;
    }

    const { newPassword, confirmPassword } = this.changepassword.value;

    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });

    const body = {
      password: newPassword,
      confirmPassword: confirmPassword
    };

    this.http.post(`${environment.apiUrl}/accounts/setup-password`, body, { headers })
      .subscribe({
        next: () => {
          this.showSuccessToast();

          // Auto logout after 2s
          setTimeout(() => {
            localStorage.clear();
            window.location.href = '/login';
          }, 2000);
        },
        error: (err) => {
          console.error('Error setting up password:', err);
          this.showErrorToast();
        }
      });
  }

  passwordsMatchValidator: ValidatorFn = (group: AbstractControl): ValidationErrors | null => {
    const newPassword = group.get('newPassword')?.value;
    const confirmPassword = group.get('confirmPassword')?.value;
    return newPassword === confirmPassword ? null : { passwordMismatch: true };
  };

  // ✅ Toggle visibility
  toggleNewPassword() {
    this.showNewPassword = !this.showNewPassword;
  }

  toggleConfirmPassword() {
    this.showConfirmPassword = !this.showConfirmPassword;
  }
}
